.badge{
    height: 70px;
    width: 140px;
    font-size: medium;
    line-height: 30px;
}
.title{
    color: rgb(30, 67, 107);
    font-weight: bold;
    text-transform: capitalize;
    font-size: 22px
}
.fade-enter{
    opacity: 0;
    transform: scale(0.6);
}
.fade-enter-active{
    opacity: 1;
    transform: scale(1);
    transition: all 600ms;
}